// @flow

import { getDecoder, getHDSetting } from '../core/networkInfo.js'
import { dataFromHex, dataToHex, XKEY_DEFAULTS, HARDENED, deriveKey } from './common.js'
import { publicKeyTweakAdd } from '../utils/secp256k1.js'
import { toXPub } from './private.js'
import { type ExtendedPublicKey } from '../../types/hd.js'

export const fromParent = async (parentKey: ExtendedPublicKey, index: number): Promise<ExtendedPublicKey> => {
  if (index >= HARDENED) throw new Error('Cannot derive hardened index from a public key')
  const { publicKey } = parentKey
  const { key, ...rest } = await deriveKey(publicKey, publicKey, index, parentKey, publicKeyTweakAdd)
  return { publicKey: key, ...rest }
}

export const fromKey = (key: $Shape<ExtendedPublicKey>, network?: string): ExtendedPublicKey => {
  const xKey = { ...XKEY_DEFAULTS, ...key }
  if (!xKey.version) {
    const { xpub: { prefix } } = getHDSetting('xpub', network)
    xKey.version = prefix
  }
  return xKey
}

export const fromHex = (keyHex: string, network?: string): ExtendedPublicKey => {
  // Get the byte which tells us what type of key we're expecting
  const headerByte = parseInt(keyHex.slice(90, 92), 16)
  if (headerByte !== 2 && headerByte !== 3) throw new Error('Wrong public key header')

  return {
    publicKey: keyHex.slice(90, 156),
    ...dataFromHex(keyHex, network)
  }
}

export const toHex = (key: ExtendedPublicKey): string =>
  dataToHex(key) + key.publicKey

export const fromString = (
  hdKey: string,
  network: string = 'main'
): ExtendedPublicKey => {
  const keyHex = getDecoder(network, hdKey.slice(0, 4)).decode(hdKey)
  return fromHex(keyHex, network)
}

export const toString = (
  hdKey: ExtendedPublicKey,
  network: string = 'main'
): string => getDecoder(network, hdKey.version).encode(toHex(hdKey))

export const fromXPriv = toXPub
